<template>
  <div class="monster-collect">
    <div class="collect-box">
       <img v-if="getLingCard === 'purpleLingCard'" class='collect-frame' src='@/assets/images/ling-card-purple.png' alt='frame'>
       <img v-else-if="getLingCard === 'greyLingCard'" class='collect-frame' src='@/assets/images/ling-card-grey.png' alt='frame'>
       <img v-else class='collect-frame' src='@/assets/images/small-frame.png' alt='frame'>
       <img class='collect-frame' src='@/assets/images/small-frame.png' alt='frame'>
       <img class='collect-frame' src='@/assets/images/small-frame.png' alt='frame'>
       <img class='collect-frame' src='@/assets/images/small-frame.png' alt='frame'>
    </div>
  </div>
</template>

<script>

export default {
  name: 'MonsterCollect',
  components: {

  },
  data () {
    return {

    }
  },
  mounted () {
  },
  methods: {

  },
  computed: {
    getLingCard () {
      return this.$store.state.getLingCard
    }
  }
}
</script>
<style lang="scss" scoped>

  .monster-collect {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);

    .collect {

      &-box {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
      }

      &-frame {
        width: 30%;
        margin: 20px;
      }

    }
  }

</style>



